html {
  font-size: var(--font-size-initial, 12px);
  font-family: "Montserrat", sans-serif; }
  html li {
    list-style-type: none; }
  html a {
    text-decoration: none;
    color: black;
    cursor: pointer; }
  html h1,
  html h2 {
    font-family: "Cormorant SC", serif;
    font-weight: normal; }
  html h1 {
    font-size: 250%; }
  html h2 {
    text-align: center;
    padding: 1rem 0;
    font-size: 150%; }
  html ul.listing.posts span.date {
    font-style: italic;
    font-weight: lighter; }
  html ::-webkit-input-placeholder {
    font-weight: lighter; }
  html :-ms-input-placeholder {
    font-weight: lighter; }
  html ::-ms-input-placeholder {
    font-weight: lighter; }
  html ::placeholder {
    font-weight: lighter; }
  html .about h3 {
    font-weight: lighter; }
  html .about .person .occupation {
    font-style: italic;
    font-weight: lighter; }

@media screen and (min-width: 480px) {
  html {
    font-size: var(--font-size-smartphone-large, 13px); } }

@media screen and (min-width: 640px) {
  html {
    font-size: var(--font-size-phablet, 14px); } }

@media screen and (min-width: 768px) {
  html {
    font-size: var(--font-size-tablet-small, 15px); }
    html h1 {
      font-size: 300%; }
    html h2 {
      font-size: 200%; } }

@media screen and (min-width: 960px) {
  html {
    font-size: var(--font-size-tablet-large, 16px); } }

@media screen and (min-width: 1024px) {
  html {
    font-size: var(--font-size-laptop, 18px); } }

@media screen and (min-width: 1600px) {
  html {
    font-size: var(--font-size-desktop, 20px); } }
